$nav-padding: 0 30px;
$nav-padding-g1500: 0 60px;

.first {
    background-color: #fff;
    justify-content: space-between;

    img {
        width: 64px;
        margin: 7px 0;
    }

    @media (max-width: 1315px) and (min-width: 1200px) {
        img {
            margin-left: 10px !important;
        }
    }

    &.nav-padding {
        padding: $nav-padding;
    }

    @media (min-width: 1500px) {
        &.nav-padding {
            padding: $nav-padding-g1500;
        }
    }

    .navbar-nav {
        position: relative;
        right: -8px;
        align-items: center;
        text-transform: uppercase;

        li {
            display: block;

            a {
                color: #640a9285;
                transition: 0.3s ease;
                padding: 9px 12px;
                letter-spacing: normal;
                font-family: Akkurat-normal;
                font-size: 12px;

                &:hover {
                    color: #fff !important;
                    background-color: #640a92;
                    text-decoration: none;
                }
            }

            @media (max-width: 1315px) and (min-width: 1200px) {
                a {
                    padding-left: 10px !important;
                    padding-right: 10px !important;
                }
            }

            &.started-button {
                position: absolute !important;
                right: 30px;

                a.btn {
                    padding: 12px 34px;
                    margin: 0;
                    border-color: #fff;
                    border-width: 2px;

                    &:hover {
                        background-color: rgb(255, 255, 255);
                        color: rgb(100, 10, 146);
                    }
                }
            }

            @media (min-width: 1500px) {
                &.started-button {
                    right: 60px;
                }
            }
        }
    }
}

